<template>
  <div class="">
    <el-table :data="tableData1" style="width: 100%">
      <el-table-column label="编号">
        <template slot-scope="scope">
          <div>
            <input type="text" />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="名称">
        <template slot-scope="scope">
          <div>
            <input type="text" />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="直径">
        <template slot-scope="scope">
          <div>
            <el-input-number v-model="num2" controls-position="right"
              >元</el-input-number
            >
          </div>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <div>
            <el-button type="primary" @click="tj">添加</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num2: "",
      tableData1: [
        {
          date: "1",
        },
      ],
    };
  },
  mounted() {},
  methods: {
    tj() {
      let ttp = this.tableData1.data + 1;
      this.tableData1.push({ data: ttp });
    },
  },
};
</script>

